@import "import/base";

.subCon{position:relative; overflow:hidden;}
.subCon.headerfix{padding-top:100px;}
.subCon{
	input[type="text"], input[type="number"], input[type="password"], input[type="email"], input[type="file"]{
		height:50px; padding:0 20px; font-size:16px; font-weight:500; color:#222; border:1px solid #ddd; background:#fff;
		&::placeholder{color:#999; font-weight:400;}
		&:-ms-input-placeholder{color:#999; font-weight:400;}
		&::-ms-input-placeholder{color:#999; font-weight:400;}
	}
	input[type="number"]::-webkit-outer-spin-button, input[type="number"]::-webkit-inner-spin-button{-webkit-appearance:none; margin:0;}
}
.subCon textarea{padding:10px; width:100%; font-size:16px; font-weight:500; color:#222; border:1px solid #ddd; background:#fff;}
.subCon select{height:50px; padding:0 50px 0 20px; font-size:16px; font-weight:500; color:#222; border:1px solid #ddd; background:#fff url("/assets/img/layout/moMenu_arrow_d.png") center right 20px no-repeat; background-size:15px auto;}

/* sub_visual */
.sub_visual{height:720px; position:relative; background-position:center center; background-size:cover; background-repeat:no-repeat;}
.sub_visual .sub_visualTxt{width:100%; padding:0 60px; position:absolute; top:50%; left:0; transform:translateY(-45%); color:#fff; text-align:center;}
.sub_visual .sub_visualTxt p{font-size:94px; font-weight:bold; line-height:1.1;}
.sub_visual .sub_visualTxt p span{font-size:110px; letter-spacing:normal; font-family:"Raleway", sans-serif;}
.sub_visual .sub_visualTxt > span{display:block; margin-top:15px; font-size:28px; line-height:1.4;}
.sub_visual .sub_visualTxt > a{margin-top:35px;}
.sub_visual .shareWrap{position:absolute; bottom:20px; right:60px; font-size:0;}
.sub_visual .shareWrap img{width:50px;}
.sub_visual .shareWrap dt{width:190px; position:absolute; top:0; right:70px;}
.sub_visual .shareWrap dt a{display:inline-block; margin-right:20px; transform:scale(0.5) rotate(-45deg); opacity:0; transition:all 0.3s ease-in-out;}
.sub_visual .shareWrap dt a:first-of-type{transition-delay:0.1s;}
.sub_visual .shareWrap dt a:nth-of-type(2){transition-delay:0.2s;}
.sub_visual .shareWrap dt a:last-of-type{margin-right:0; transition-delay:0.3s;}
.sub_visual .shareWrap dt.on a{transform:scale(1) rotate(0deg); opacity:1;}
.sub_visual .shareWrap dt.on a:first-of-type{transition-delay:0.3s;}
.sub_visual .shareWrap dt.on a:nth-of-type(2){transition-delay:0.2s;}
.sub_visual .shareWrap dt.on a:last-of-type{margin-right:0; transition-delay:0.1s;}
.sub_visual .shareWrap dd{display:inline-block; cursor:pointer;}
